@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,500;0,700;0,900;1,100;1,300;1,500;1,700;1,900&display=swap);
*{width:100%;margin:0;padding:0;box-sizing:border-box}body{background-color:#080808;color:#b8b8b8}.label-sub{padding-bottom:16px;padding-top:32px}.shelf-divider{width:100%;height:1px;background-color:rgba(255,255,255,0.1);margin:24px 0px}.App{font-family:'Helvetica', sans-serif;padding:16px}.App .home{z-index:90}.App .home .search{width:100%}.App .home .search #search-home{height:32px;width:calc( 100% - 8px * 8);margin-bottom:16px;background:rgba(255,255,255,0.1);border:0px;border-radius:8px}.App .home .genres .genres-list{display:grid;grid-template-rows:1fr 1fr;grid-auto-flow:column;height:112px;overflow-y:scroll}.App .home .genres .genres-list .genre{display:grid;align-items:center;justify-items:start;grid-template-columns:48px auto;grid-gap:8px;width:40vw;margin:0px 8px 8px 0px;background:rgba(75,75,75,0.514);color:white;border-radius:4px}.App .home .genres .genres-list .genre-icon{width:48px;height:48px;background-size:100%}.App .home .mood .label-sub{display:flex}.App .home .mood .label-sub::after{width:6rem;content:"View Playlist";font-size:0.8rem;color:#1cb854;text-align:right}.App .home .mood .mood-strip{display:grid;grid-template-rows:1fr;grid-auto-flow:column;height:30vw;overflow-y:scroll}.App .home .mood .mood-strip .song{display:grid;width:30vw;height:30vw;margin:0px 8px 8px 0px;padding:8px;background:rgba(124,124,124,0.514);border-radius:4px}.App .view{width:100vw;height:100vh;position:fixed;top:0vh;left:100vw;background-color:#101010;z-index:100;color:white;transition-duration:0.2s;overflow:scroll}.App .view.expand{top:0vh;left:0vw;transition-duration:0.3s}.App .view .view__head .back{padding:16px}.App .view .view__body{padding:32px}.App .view .view__body .playlist .track{display:grid;grid-template-columns:64px auto 32px;align-content:center;grid-gap:32px;padding:16px 0px}.App .view .view__body .playlist .track .album-artwork{width:64px;height:64px;background:white;border-radius:2px}.App .view .view__body .playlist .track .track-name{display:grid;align-content:center;font-size:1.1;font-weight:500}.App .view .view__body .playlist .track .track-name .artist{opacity:0.7;font-weight:300;margin-top:8px}.App .view .view__body .playlist .track .play-button{display:grid;align-content:center;justify-content:end;background-size:100%;background-blend-mode:overlay}.App .view .view__body .playlist .track .play-button path{fill:#1db954}.App .model{display:grid;grid-template-rows:64px auto;width:100vw;height:calc(100vh - 8px*2);position:fixed;top:100vh;right:0vw;overflow:scroll;background-color:#f3eded;border-radius:8px 8px 0px 0px;z-index:110;transition-duration:0.3s;-webkit-transform:rotateX(90deg);transform:rotateX(90deg)}.App .model.expand{top:16px;left:0vw;-webkit-transform:rotateX(0deg);transform:rotateX(0deg);transition-duration:0.3s}.App .model .model__head .back{padding:16px}.App .model .model__body{height:100%}.App .model .model__body .track{display:grid;grid-template-rows:45% 25% 30%;grid-gap:16px;height:100%;padding:0px 32px;color:black;text-align:left}.App .model .model__body .track .album-artwork{background-image:url(/static/media/artwork.33276b98.jpg);background-size:auto 100%;background-repeat:no-repeat;background-position:center bottom}.App .model .model__body .track .track-name{font-weight:600;font-size:1.3rem}.App .model .model__body .track .track-name .spotify-logo{width:100%;height:32px;background-image:url(/static/media/Spotify_Logo_RGB_Green.0c4ae91b.png);background-size:auto 60%;background-repeat:no-repeat;background-position:left bottom;margin:16px 0 8px 0}.App .model .model__body .track .play-controls{padding:0px 0px}.App .model .model__body .track .play-controls .controls{display:grid;grid-template-columns:1fr 1fr 1fr;padding:32px}.App .model .model__body .track .play-controls .controls .control{display:grid;align-content:center}.App .model .model__body .track .play-controls .controls .control svg{height:48px}.App .model .model__body .track .play-controls .controls .control.next svg{height:40px}.App .model .model__body .track .play-controls .progress-bar{width:100%;height:4px;border-radius:1px;background:rgba(128,128,128,0.596)}.App .model .model__body .track .play-controls .progress-bar .progress{width:35%;height:90%;background-color:#2c2c2c}.App .model .model__body .track .play-controls .progress-bar .progress-time{display:grid;grid-template-columns:1fr 1fr;width:100%;margin-top:8px;color:grey;font-size:0.8rem}.App .model .model__body .track .play-controls .progress-bar .progress-time .current-time{text-align:left}.App .model .model__body .track .play-controls .progress-bar .progress-time .remaining-time{text-align:right}.App .model .model__body .track .artist{font-weight:400;font-size:1.1rem}

